<template>
  <form @submit.prevent="submitWorkers" class="flex-center" >
    <div class="input-field margin-fix right-margin-little">
      <input
        class="input-add"
        type="tel"
        id="worker"
        v-model.trim="number"
        v-mask="'+7 (###) ###-##-##'"
        placeholder="–ù–æ–º–µ—Ä –Ω–æ–≤–æ–≥–æ —Ä–∞–±–æ—á–µ–≥–æ"
        :class="{invalid: ($v.number.$dirty && !$v.number.required) || ($v.number.$dirty && !$v.number.minLength)}"
      >
      <small
        class="helper-text invalid"
        v-if="$v.number.$dirty && !$v.number.required"
      >
        –í–≤–µ–¥–∏—Ç–µ –Ω–æ–º–µ—Ä —Ä–∞–±–æ—á–µ–≥–æ
      </small>
      <small
        class="helper-text invalid"
        v-else-if="$v.number.$dirty && !$v.number.minLength"
      >
        –ù–æ–º–µ—Ä –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å –Ω–µ –º–µ–Ω–µ–µ {{$v.number.$params.minLength.min}} —Å–∏–º–≤–æ–ª–æ–≤.
      </small>
      <small
        class="helper-text invalid"
        v-else-if="coincidence"
      >
        –ù–æ–º–µ—Ä —É–∂–µ –µ—Å—Ç—å
      </small>
    </div>

    <button class="btn-transparent transparent btn-page-title blue-text text-darken-1" title="–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤–æ–≥–æ —Ä–∞–±–æ—á–µ–≥–æ" type="submit">
      <i class="material-icons big-material-icons">add</i>
    </button>
  </form>
</template>

<script>
import { required, minLength } from 'vuelidate/lib/validators'
import { mask } from 'vue-the-mask'

export default {
  name: 'AddCardSWorkers',
  data () {
    return {
      number: '',
      coincidence: false
    }
  },
  props: ['workers'],
  directives: { mask },
  validations: {
    number: { required, minLength: minLength(7) }
  },
  methods: {
    createWorker () {
      if (this.number.trim() && !this.coincidence) {
        // Body New Worker
        const newWorker = {
          id: Date.now(),
          name: '–ù–æ–≤—ã–π',
          surname: '–†–∞–±–æ—á–∏–π',
          patronymic: '',
          accountNumber: Date.now(),
          nameCard: '–ù–æ–≤—ã–π',
          surnameCard: '–†–∞–±–æ—á–∏–π',
          patronymicCard: '',
          accountNumberCard: '',
          bank: '–°–±–µ—Ä–ë–∞–Ω–∫',
          age: '',
          sex: '–ú—É–∂—Å–∫–æ–π',
          city: '',
          nationality: '',
          passportID: '',
          passportDate: '',
          passportIssued: '',
          registration: '',
          address: '',
          homePhone: '',
          mobilePhone: this.number,
          medicalBook: '',
          education: '',
          university: '',
          previousWork: '',
          reasonComing: '',
          professions: '',
          nightShift: '–ù–µ—Ç',
          checkMVD: '–î–∞',
          dateInterview: '',
          uniform: '',
          fired: '–ù–µ—Ç',
          edited: false,
          editedCount: 0
        }

        this.$emit('add-worker', newWorker)
        this.number = ''

        // this.$router.push(`/workers/edit/worker${newWorker.id}`)
      }
    },

    submitWorkers () {
      // For validations
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }

      this.createWorker()
    }
  },
  mounted () {
  }
}
</script>
